import { Button, TextField } from "@material-ui/core";
import React, { useState } from "react";
import { useFormControls } from "./ContactFormControls.tsx";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { green, purple } from "@material-ui/core/colors";
import PhoneRoundedIcon from "@material-ui/icons/PhoneRounded";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import MailIcon from "@material-ui/icons/Mail";
import XingIcon from "./XingIcon.js";

const useStyles = makeStyles((theme) => ({
  root: {
    // backgroundColor: "red",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 10,
      marginRight: 10,
      marginTop: 100,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 100,
      display: "flex",
      justifyContent: "space-evenly",
    },
  },
  form: {
    [theme.breakpoints.up("sm")]: {
      marginTop: 64,
      padding: 20,
    },
    [theme.breakpoints.down("sm")]: {
      marginTop: 64,
      marginBottom: 64,
      padding: 10,
    },
    border: "1px solid black",
    background: "linear-gradient(45deg, #4298f5 30%, #0051a8 90%)",
    color: "white",
  },
  contactDetail: {
    [theme.breakpoints.up("sm")]: {
      marginTop: 64,
      padding: 20,
    },
    [theme.breakpoints.down("sm")]: {
      marginTop: 64,
      padding: 10,
    },
    border: "1px solid black",
    background: "linear-gradient(45deg, #4298f5 30%, #0051a8 90%)",
    color: "white",
  },
  buttonDiv: {
    marginTop: 30,
  },
  sendMessage: {
    color: "green",
  },
  item: {
    [theme.breakpoints.up("sm")]: {
      padding: 10,
    },
    // backgroundColor: "lightgray",
  },
}));

const inputFieldValues = [
  {
    name: "fullName",
    label: "Vorname und Nachname",
    id: "my-name",
  },
  {
    name: "email",
    label: "E-Mail Adresse",
    id: "my-email",
  },
  {
    name: "message",
    label: "Nachricht",
    id: "my-message",
    multiline: true,
    rows: 10,
  },
];

const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText(purple[500]),
    backgroundColor: "white",
    color: "#4298f5",
    "&:hover": {
      backgroundColor: purple[500],
      color: "white",
    },
  },
}))(Button);

export default function Kontakt() {
  const classes = useStyles();
  const [send, setSend] = useState("false");
  let successMessage;
  if (send === "true") {
    successMessage = (
      <Typography variant="h6">Anfrage erfolgreich gesendet</Typography>
    );
  }
  console.log("status" + send);
  const { handleInputValue, handleFormSubmit, formIsValid, errors } =
    useFormControls();
  return (
    <div className={classes.root}>
      <div className={classes.contactDetail}>
        <Typography variant="h4" align="center">
          GET IN TOUCH
        </Typography>
        <div className={classes.item}>
          <List>
            <ListItem>
              <ListItemIcon>
                <PhoneRoundedIcon />
              </ListItemIcon>
              <ListItemText>
                <Typography variant="h6" align="left">
                  +49 15165908580
                </Typography>
              </ListItemText>
            </ListItem>
          </List>
        </div>
        <div className={classes.item}>
          <List>
            <ListItem>
              <ListItemIcon>
                <MailIcon />
              </ListItemIcon>
              <ListItemText>
                <Typography variant="h6" align="left">
                  henning1.weise@googlemail.com
                </Typography>
              </ListItemText>
            </ListItem>
          </List>
        </div>
        <div className={classes.item}>
          <List>
            <ListItem>
              <ListItemIcon>
                <XingIcon />
              </ListItemIcon>
              <ListItemText>
                <Typography variant="h6" align="left">
                  henning1.weise@googlemail.com
                </Typography>
              </ListItemText>
            </ListItem>
          </List>
        </div>
      </div>

      <div className={classes.form}>
        <Typography variant="h4" align="center">
          SEND ME A MESSAGE
        </Typography>
        <form autoComplete="off" onSubmit={handleFormSubmit}>
          {inputFieldValues.map((inputFieldValue, index) => {
            return (
              <TextField
                key={index}
                onChange={handleInputValue}
                onBlur={handleInputValue}
                name={inputFieldValue.name}
                label={inputFieldValue.label}
                error={errors[inputFieldValue.name]}
                multiline={inputFieldValue.multiline ?? false}
                fullWidth
                rows={inputFieldValue.rows ?? 1}
                autoComplete="none"
                {...(errors[inputFieldValue.name] && {
                  error: true,
                  helperText: errors[inputFieldValue.name],
                })}
              />
            );
          })}
          <div className={classes.buttonDiv}>
            <ColorButton
              variant="contained"
              type="submit"
              color="secondary"
              onClick={() => setSend("true")}
              disabled={!formIsValid()}
            >
              Anfrage senden
            </ColorButton>
          </div>
          <div className={classes.sendMessage}>{successMessage}</div>
        </form>
      </div>
    </div>
  );
}
