import React from "react";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import FastfoodIcon from "@material-ui/icons/Fastfood";
import LaptopMacIcon from "@material-ui/icons/LaptopMac";
import HotelIcon from "@material-ui/icons/Hotel";
import RepeatIcon from "@material-ui/icons/Repeat";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles((theme) => ({
  root: {
    // backgroundColor: "green",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 100,
    },
    display: "flex",
    justifyContent: "center",
  },
  paper: {
    width: "auto",
    backgroundColor: "gray",
    padding: "1%",
  },
  paperv2: {
    marginLeft: "10%",
    marginTop: "2%",
    padding: "1%",
  },
  timelineContentContainer: {
    textAlign: "left",
  },
  timelineContent: {
    display: "inline-block",
    transform: "rotate(-90deg)",
    textAlign: "center",
    minWidth: 50,
  },
  timelineIcon: {
    transform: "rotate(-90deg)",
  },
  content: {
    [theme.breakpoints.up("sm")]: {
      width: "80%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
}));

export default function Berufserfahrung() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <div className={classes.content}>
        <Timeline className={classes.timeline} align="alternate">
          <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                2009
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                  Abitur
                </Typography>
                <Typography>Schlossgymnasium Kirchheim unter Teck</Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>

          <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                Juli 2017
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                  Fitness and Sports Entertainer
                </Typography>
                <Typography>Einsatz auf Lanzarote</Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>

          <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                November 2017
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                  Arbeiten
                </Typography>
                <Typography>
                  Daimler AG im Bereich Logistik, Holder GmbH und Scholderbeck
                  GmbH &amp; Co. KG{" "}
                </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>

          <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                Herbst 2018
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                  Studium Softwaretechnik
                </Typography>
                <Typography>Studium an der Universität Stuttgart </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>

          <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                Sommer 2019
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                  Studium Softwaretechnik &amp; Medieninformatik
                </Typography>
                <Typography>
                  Studium an der Hochschule Esslingen mit Schwerpunkt
                  Medieninformatik{" "}
                </Typography>
              </Paper>
              <Paper elevation={3} className={classes.paperv2}>
                <Typography variant="h6" component="h1">
                  Tutor Mathematik Vorkurs
                </Typography>
                <Typography>
                  Regelmäßig Tutor beim Mathematik Vorkurs{" "}
                </Typography>
              </Paper>
              <Paper elevation={3} className={classes.paperv2}>
                <Typography variant="h6" component="h1">
                  HiWi Job
                </Typography>
                <Typography>
                  Leitung eines Tutoriums im Bereich Mathematik{" "}
                </Typography>
              </Paper>
              <Paper elevation={3} className={classes.paperv2}>
                <Typography variant="h6" component="h1">
                  Praxissemester
                </Typography>
                <Typography>
                  ab September als Webentwickler tätig bei W11K GmbH{" "}
                </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>

          <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                Heute
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot />
            </TimelineSeparator>
            <TimelineContent></TimelineContent>
          </TimelineItem>
        </Timeline>
      </div>
    </div>
  );
}
