import React from "react";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import LinearProgress from "@material-ui/core/LinearProgress";
import {
  createMuiTheme,
  responsiveFontSizes,
  ThemeProvider,
} from "@material-ui/core/styles";

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

theme.typography.h4 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "1.3rem",
  },
};

theme.typography.h6 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "0.8rem",
  },
};

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 10,
    borderRadius: 5,
  },
  colorPrimary: {
    backgroundColor:
      theme.palette.grey[theme.palette.type === "light" ? 400 : 700],
  },
  bar: {
    borderRadius: 5,
    backgroundColor: "#4592b0",
  },
}))(LinearProgress);

const useStyles = makeStyles((theme) => ({
  root: {
    // background: "linear-gradient(135deg, #bfb8d4, #232423)",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 64,
    },
    height: "93.2vh",
  },
  programmierenContent: {
    // marginLeft: "5%",
    paddingTop: "2%",
    [theme.breakpoints.up("sm")]: {
      width: "30%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "10%",
    },
  },
  anwendungenContent: {
    paddingTop: "2%",
    // marginLeft: "5%",
    [theme.breakpoints.up("sm")]: {
      width: "30%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
      paddingLeft: "10%",
      paddingRight: "10%",
      marginTop: "5%",
      paddingBottom: "10%",
    },
  },
  upperContent: {
    [theme.breakpoints.up("sm")]: {
      display: "flex",
      justifyContent: "space-evenly",
    },
    [theme.breakpoints.down("sm")]: {
      justifyContent: "center",
    },
  },
  lowerContent: {
    marginTop: "2%",
    [theme.breakpoints.up("sm")]: {
      display: "flex",
      justifyContent: "space-evenly",
    },
  },
}));

export default function Skills() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <div className={classes.upperContent}>
        <div className={classes.programmierenContent}>
          <ThemeProvider theme={theme}>
            <Typography variant="h4" component="h1" gutterBottom>
              Programmierung
            </Typography>

            <Typography variant="h6" component="h1" gutterBottom>
              Java
            </Typography>
            <BorderLinearProgress variant="determinate" value={50} />

            <Typography variant="h6" component="h1" gutterBottom>
              JavaScript &lang; NodeJS, ThreeJS, React &rang;
            </Typography>
            <BorderLinearProgress variant="determinate" value={70} />

            <Typography variant="h6" component="h1" gutterBottom>
              C &amp; C++
            </Typography>
            <BorderLinearProgress variant="determinate" value={35} />

            <Typography variant="h6" component="h1" gutterBottom>
              HTML &amp; CSS
            </Typography>
            <BorderLinearProgress variant="determinate" value={80} />

            <Typography variant="h6" component="h1" gutterBottom>
              SQL
            </Typography>
            <BorderLinearProgress variant="determinate" value={30} />

            <Typography variant="h6" component="h1" gutterBottom>
              DB2
            </Typography>
            <BorderLinearProgress variant="determinate" value={15} />
          </ThemeProvider>
        </div>

        <div className={classes.anwendungenContent}>
          <Typography variant="h4" component="h1" gutterBottom>
            Anwendungen
          </Typography>

          <Typography variant="h6" component="h1" gutterBottom>
            Word, Excel &amp; Powerpoint
          </Typography>
          <BorderLinearProgress variant="determinate" value={50} />

          <Typography variant="h6" component="h1" gutterBottom>
            LateX
          </Typography>
          <BorderLinearProgress variant="determinate" value={70} />

          <Typography variant="h6" component="h1" gutterBottom>
            Photoshop &amp; After Effects
          </Typography>
          <BorderLinearProgress variant="determinate" value={35} />

          <Typography variant="h6" component="h1" gutterBottom>
            Versionsverwaltungsoftware Git
          </Typography>
          <BorderLinearProgress variant="determinate" value={80} />

          <Typography variant="h6" component="h1" gutterBottom>
            Virtual Box
          </Typography>
          <BorderLinearProgress variant="determinate" value={30} />

          <Typography variant="h6" component="h1" gutterBottom>
            Wireshark
          </Typography>
          <BorderLinearProgress variant="determinate" value={15} />

          <Typography variant="h6" component="h1" gutterBottom>
            Postman
          </Typography>
          <BorderLinearProgress variant="determinate" value={15} />
        </div>
      </div>
      <div className={classes.lowerContent}>
        <div className={classes.programmierenContent}>
          <Typography variant="h4" component="h1" gutterBottom>
            Betriebssysteme
          </Typography>

          <Typography variant="h6" component="h1" gutterBottom>
            Windows
          </Typography>
          <BorderLinearProgress variant="determinate" value={50} />

          <Typography variant="h6" component="h1" gutterBottom>
            Linus
          </Typography>
          <BorderLinearProgress variant="determinate" value={70} />
        </div>

        <div className={classes.anwendungenContent}>
          <Typography variant="h4" component="h1" gutterBottom>
            IDE's
          </Typography>

          <Typography variant="h6" component="h1" gutterBottom>
            IntelliJ
          </Typography>
          <BorderLinearProgress variant="determinate" value={50} />

          <Typography variant="h6" component="h1" gutterBottom>
            Eclipse
          </Typography>
          <BorderLinearProgress variant="determinate" value={70} />

          <Typography variant="h6" component="h1" gutterBottom>
            VS Code &amp; VS Studio Code
          </Typography>
          <BorderLinearProgress variant="determinate" value={35} />
        </div>
      </div>
    </div>
  );
}
