import React from "react";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Projectcard from "./Projectcard.js";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "gray",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 64,
    },
  },
  content: {
    [theme.breakpoints.up("sm")]: {
      display: "flex",
    },
  },
  wrapper1: {
    [theme.breakpoints.up("sm")]: {
      height: "93.2vh",
      width: "25%",
      paddingTop: "5%",
      paddingLeft: "3%",
    },
    // backgroundColor: "red",
  },
  wrapper2: {
    [theme.breakpoints.up("sm")]: {
      height: "93.2vh",
      width: "25%",
      paddingTop: "25%",
      paddingLeft: "2%",
      paddingRight: "2%",
    },
    // backgroundColor: "green",
  },
  wrapper3: {
    [theme.breakpoints.up("sm")]: {
      height: "93.2vh",
      width: "25%",
      paddingTop: "12%",
    },
  },
  wrapper4: {
    [theme.breakpoints.up("sm")]: {
      height: "93.2vh",
      width: "25%",
      paddingTop: "33%",
    },
  },
}));

export default function Kontakt() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <div className={classes.content}>
        <div className={classes.wrapper1}>
          <div>
            <Projectcard
              heading={"WebApp mit MERN-Stack"}
              description={
                <span>
                  <Typography paragraph>
                    Diese WebApp ist im Zuge des Moduls "Projekt
                    Medieninformatik" entstanden. Ziel war es, eine
                    hochschulinterne WebApp für Kleinanzeigen zu entwickeln.{" "}
                  </Typography>
                  <Typography paragraph>Technische Besonderheiten:</Typography>
                  <Typography paragraph>
                    - Login-System mit Keycloak und dem SAML-Protokoll <br />-
                    Verwendung von Material-UI <br />- Anbindung an MongoDB mit
                    Hilfe von Mongoose
                    <br />
                    - Echtzeitchat mittels Socket.io
                    <br />- Verwendung von Docker
                  </Typography>
                </span>
              }
            />
          </div>
        </div>
        <div className={classes.wrapper2}>
          <Projectcard
            heading={"VR-Game mit ThreeJS"}
            url={
              "https://www2.hs-esslingen.de/~heweit00/mci-2/Game-VR/index.html"
            }
            description={
              <span>
                <Typography paragraph>
                  Im Zuge des Moduls "Mensch-Computer-Interaktion" habe ich
                  dieses VR-Game für die OculusGo mit ThreeJS programmiert.{" "}
                </Typography>
              </span>
            }
          />
        </div>
        <div className={classes.wrapper3}>
          <Projectcard
            heading={"Mobile-Game mit JS Canvas"}
            url={"https://www2.hs-esslingen.de/~heweit00/mci-2/Game/index.html"}
            description={
              <span>
                <Typography paragraph>
                  Dieses Handyspiel wurde nur mit Hilfe des JavaScript Canvas
                  erstellt. Der Fokus des Spiels liegt hier auf dem Einsatz der
                  unterschiedlichen Touch-Interaktionen wie "drücken",
                  "wischen", "halten"{" "}
                </Typography>
              </span>
            }
          />
        </div>
        <div className={classes.wrapper4}>
          <Projectcard
            heading={"Website mit HTML und CSS inklusive JS-Game"}
            url={"https://www2.hs-esslingen.de/~heweit00"}
            description={
              <span>
                <Typography paragraph>
                  Im Modul "Internet-Technologien" hatte ich die Aufgabe, die
                  Website der Hochschule selber nachzubauen.{" "}
                </Typography>
              </span>
            }
          />
        </div>
      </div>
    </div>
  );
}
